import React, { Component } from 'react';
import Menu from './components/Menu';

class App extends Component {
  render() {
    return (
      <div className="lunch">
        <div className="lunch__wrap">
          <div className="lunch__omega">
            <h1>This is the &apos;beta&apos; version of lunch.email</h1>
            <h2>In fact... it&apos;s so beta that I&apos;ve opted to call it omega.</h2>
          </div>

          <div className="roadmap">
            <p>Some project goals...</p>
            <ul>
              <li className="roadmap--complete">Switch from garbage hackjob to a React hackjob.</li>
              <li className="roadmap--complete">Add cool ass animations (some / some more).</li>
              <li>Add some sort of lunch based dashboard.</li>
              <li>Add some sort of reviews component.</li>
              <li>Run reviews through sentiment analysis.</li>
              <li>Machine learning to optimize the lunch options.</li>
            </ul>
          </div>

          <Menu/>
        </div>
        <div className="gradient"></div>
      </div>
    );
  }
}

export default App;



